<template>
  <div class="main">
    <div class="cartop">
      购物车({{ list.length }})
      <span class="iconfont icon-lajitong" @click="delGoods"></span>
    </div>
    <div class="goodslist">
      <div class="item" v-for="item in list" :key="item.id">
        <div class="itemleft">
          <van-checkbox
            :value="item.checked"
            @click="changeCheckedOne(item)"
          ></van-checkbox>
        </div>
        <div class="itemmidd">
          <van-image width="100%" height="100%" :src="item.img" />
        </div>
        <div class="itemright">
          <div class="itemright-top">
            <div>{{ item.goodsname }}</div>
            <div>{{ item.colorname }}；{{ item.ggname }}</div>
          </div>
          <div class="itemright-bot">
            <div class="price">
              ¥<span>{{ item.price.toFixed(2) }}</span>
            </div>
            <div class="num">
              <div class="sub">
                <span
                  class="iconfont icon-jianhao"
                  :class="{ disabled: item.num <= 1 }"
                  @click="subNum(item)"
                ></span>
              </div>
              <div class="inp">
                <input
                  type="number"
                  :value="item.num"
                  @blur="inpNum($event, item)"
                />
              </div>
              <div class="add">
                <span
                  class="iconfont icon-jiahao1"
                  :class="{ disabled: item.num >= 999 }"
                  @click="addNum(item)"
                ></span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="carbot">
      <div class="carbot-left">
        <van-checkbox class="checkall" v-model="checkedAll" @click="checkedAllchange"></van-checkbox>
        全选
      </div>
      <div class="carbot-midd">
        <span
          >合计：<span>¥ {{ price }}</span></span
        >
      </div>
      <div class="carbot-right" @click="onOrder">
        <div class="btnup">结算</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      checkedAll: false,
    };
  },
  computed: {
    ...mapState(["userinfo"]),
    ...mapState("shoppingcar", ["list"]),
    ...mapGetters("shoppingcar", ["price"]),
  },
  methods: {
    ...mapMutations("shoppingcar", ["updataCheckedAll", "updataCheckedOne"]),
    ...mapActions("shoppingcar", ["changeNum", "delCarList"]),
    changeCheckedOne(item) {
      this.updataCheckedOne(item);
      this.watchCheckedAllchange();
    },

    checkedAllchange() {
      this.updataCheckedAll(this.checkedAll)
    },
    watchCheckedAllchange() {
      if (this.list.length) {
        this.checkedAll = this.list.every((item) => item.checked);
      } else {
        this.checkedAll = false;
      }
    },
    delGoods() {
      let list = this.list.filter((item) => item.checked);
      if (list.length) {
        this.$dialog
          .confirm({
            title: "删除",
            message: "是否删除选中的商品?",
          })
          .then(() => {
            this.delCarList(this.list);
          })
          .catch(() => {});
      } else {
        this.$toast("选择商品后才可以删除哦");
      }
    },
    subNum(item) {
      if (item.num > 1) {
        this.changeNum({
          id: item.id,
          num: item.num - 1,
        });
      } else {
        this.$toast("最少选择一件");
      }
    },
    addNum(item) {
      if (item.num < 999) {
        this.changeNum({
          id: item.id,
          num: item.num + 1,
        });
      } else {
        this.$toast("超出最大购买上限");
      }
    },
    inpNum(e, item) {
      if (e.target.value < 1) {
        this.$toast("输入的值最少一件");
        e.target.value = item.num;
        return false;
      }
      if (e.target.value > 999) {
        this.$toast("超出最大购买上限");
        e.target.value = item.num;
        return false;
      }
      this.changeNum({
        id: item.id,
        num: e.target.value * 1,
      });
    },
    onOrder() {
      if (this.list.some((item) => item.checked)) {
        this.$router.push("/pay");
      } else {
        this.$toast("选择商品后才可以结算哦");
      }
    },
  },
  created() {
    this.watchCheckedAllchange();
  },
};
</script>

<style lang="scss" scoped>
.main {
  padding-top: 42px;
  .cartop {
    z-index: 1;
    width: 100%;
    height: 42px;
    background: #fff;
    border-bottom: 0.5px solid #e8e8e8;
    position: fixed;
    top: 0;
    text-align: center;
    line-height: 42px;
    font-size: 16px;
    .icon-lajitong {
      position: absolute;
      right: 8px;
      font-size: 18px;
      font-weight: 300;
      color: #a4a5aa;
    }
  }
  .goodslist {
    padding-bottom: 126px;
    .item {
      margin: 10px 10px 0 10px;
      background: #fff;
      width: 355px;
      height: 120px;
      padding: 10px 10px 10px 0;
      box-sizing: border-box;
      display: flex;
      .itemleft {
        width: 40px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .itemmidd {
        width: 100px;
        height: 100%;
      }
      .itemright {
        width: 195px;
        height: 100%;
        padding-left: 10px;
        .itemright-top {
          width: 100%;
          height: 66px;
          div {
            width: 100%;
            height: 33px;
            color: #363131;
            font-size: 13px;
            line-height: 33px;
            overflow: hidden;
          }
        }
        .itemright-bot {
          .disabled {
            color: rgba($color: #999, $alpha: 0.2);
          }
          width: 100%;
          height: 34px;
          line-height: 34px;
          display: flex;
          .price {
            color: #e84040;
            font-size: 14px;
            flex: 1;
            span {
              font-size: 15px;
            }
          }

          .num {
            width: 100px;
            height: 100%;
            display: flex;
            justify-content: space-around;
            .inp {
              input {
                border: none;
                background: none;
                width: 30px;
                text-align: center;
              }
            }
          }
        }
      }
    }
  }
  .carbot {
    width: 100%;
    height: 75px;
    background: #fff;
    border-top: #e8e8e8 1px solid;
    position: fixed;
    bottom: 50px;
    display: flex;
    .carbot-left {
      width: 100px;
      height: 100%;
      display: flex;
      align-items: center;
      .checkall {
        margin: 0 10px;
      }
    }
    .carbot-midd {
      width: 160px;
      display: flex;
      align-self: center;
      flex-direction: row-reverse;
      span {
        span {
          color: #e84040;
          font-weight: 600;
        }
      }
    }
    .carbot-right {
      width: 115px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      .btnup {
        text-align: center;
        width: 96px;
        height: 37px;
        line-height: 37px;
        color: #fff;
        font-size: 17px;
        text-align: center;
        background: #e84040;
        border-radius: 18px;
      }
    }
  }
}
</style>